import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {Link, withRouter} from "react-router-dom";

const Detail = props => {
    if (props.settings) {
        return (
            <div className="container">
                <div className="top_text">
                     
                </div>
                <div className="line">
                    <div className="column-1">
                        <div className="text-big">{props.settings.currency}</div>
                        <div className="text-small"></div>
                    </div>
                    <div className="column">
                        <div className="text-big">{props.settings.currency === 'usd' && '$'} {props.settings.capital} {props.settings.currency === 'uah' && ''}</div>
                        <div className="text-small"></div>
                    </div>
                </div>
                <div className="line">
                    <div className="column-1">
                        <div className="text-big">{props.settings.period} </div>
                        <div className="text-small"></div>
                    </div>
                    <div className="column">
                        <div className="text-big">{props.settings.interestPayment === "monthly" ? "" : "  "}</div>
                        <div className="text-small"></div>
                    </div>
                </div>
                <div className="line">
                    <div className="column-1">
                        <div className="text-big">{props.settings.rate}</div>
                        <div className="text-small"> </div>
                    </div>
                </div>
                <div className="top_text">
                      
                </div>
                <div className="line final-count">
                    <div className="column-1">
                        <div className="text-big">{props.settings.currency === 'usd' && '$'} {props.settings.incomeTotal} {props.settings.currency === 'uah' && ''}</div>
                        <div className="text-small">   </div>
                    </div>
                    <div className="column">
                        <div className="text-big">{props.settings.currency === 'usd' && '$'} {props.settings.incomeMonthly} {props.settings.currency === 'uah' && ''}</div>
                        <div className="text-small"></div>
                    </div>
                </div>
                <div>
                    <Link to={'/'} className="button_accented"></Link>
                </div>
            </div>
        )
    } else {
        return (
            <div className="container">
                <div className="top_text">
                          
                </div>
                <Link className="button_accented" to={'/'}> </Link>
            </div>
        )
    }
}

Detail.propTypes = {
    settings: PropTypes.object
};

const mapStateToProps = state => ({
    settings: state.invest.settings,
});

export default withRouter(connect(mapStateToProps)(Detail));


